<template>
    <select :value="modelValue" @change="changeOption">
        <option disabled value="">Выберите из списка</option>
        <option 
            v-for="option in options"
            :key="option.value"
            :value="option.value"
        >
                {{ option.name }}
        </option>
    </select>
</template>

<script>
export default {
   name: 'my-select',
   props: {
    modelValue: {
        type: String
    },
    options: {
        type: Array,
        default: () => [], 
    }
   },
   methods: {
    changeOption(event) {
        this.$emit('update:modelValue', event.target.value)
    }
   }
}
</script>

<style scoped>
    select{
        width: 197px;
        padding: 10px 15px;
        color: green;
        border: 1px solid green;
    }
</style>